import React from "react";

const SimpleTBody = (props)=>{
    const {removeEmployee: cancellaImpiegato, employees: impiegati} = props
    //console.log('SimpleTBody cancellaImpiegato: ',cancellaImpiegato);
    const rows = impiegati.map(
        (impiegato, i) => <tr key={i}>
            <td>{impiegato.name}</td>
            <td>
                <button onClick={()=>cancellaImpiegato(i)}>Delete {impiegato.name}</button>
            </td>
        </tr>
    );
    return <tbody>{rows}</tbody>

}

export default SimpleTBody
