import './index.css';
import React from 'react';
import ReactDOM from 'react-dom';
import Table from "./Table";
import SimpleDiv from "./SimpleDiv";
import SimpleTable from "./SimpleTable";
import Form from "./Form";
import Api from "./Api";
class FirstApp extends React.Component{
    employees = [
        {name: 'Max', job: 'Dev'},
        {name: 'Marino', job: 'Analyst'},
        {name: 'Andrea', job: 'Des'}
    ];
    state = {
        employees: this.employees,
    }
    addEmployee = (newEmployee)=>{
        console.log('adding employee...',newEmployee);
        this.setState(
            {
                //employees: this.state.employees.unshift(newEmployee),//Use immutable values
                employees: [newEmployee, ...this.state.employees],
            }
        )
    }
    removeEmployee = (index) => {
        //console.log('removing employee...',index);
        const newEmployees = this.state.employees.filter(
            (emp, n) => n !== index
        );
        //Very important: to (re)set state use !! setState() !!
        this.setState(
            {
                    employees: newEmployees
            }
            )
        }

    constructor() {
        super();
        console.log('FirstApp state: ',this.state);
    }
    render(){
        const name = "Fabio"

        return (
            <main>
                <h1 className='title'>React Hi!</h1>
                <p>My First App, <em className='good'>{name}</em></p>
                <Api></Api>
                <Form addEmployee = {this.addEmployee}/>
                <SimpleTable employees = {this.state.employees} removeEmployee = {this.removeEmployee}></SimpleTable>
                <SimpleDiv></SimpleDiv>
                <Table employeesData={this.employees}></Table>
            </main>
        )

    }
    greetings(){
        console.log('Uella');
    }
}

const root = document.querySelector('#root');
ReactDOM.render(<FirstApp/>,root);

console.log('Hi React!');


