import React from "react";

export const ApiOutput = (props) => {
    const data = props.data || ['',[],[],[]];
    console.log(data);
    const [resources,urls] = [data[1],data[3]];
    console.log(resources,urls);
    const rows = resources.map(
        (res,i) => {
            const link = urls[i];
            return (
                <li key={i} className="list-item">
                    <div>
                        {res}
                    </div>
                    <div>
                        <a href={link}>{link}</a>
                    </div>
                </li>
            )
        }
    )
    return (
            <ul className="list">
                {rows}
            </ul>
        );
}
