import React from 'react';
import SimpleTBody from "./SimpleTBody";
const SimpleTable = (props) => {
    //
    const {removeEmployee,employees} = props;
    //console.log('Hi SimpleTable!');
    return <div>
        <p className="good">
            Simple Table, Removing Items
        </p>
        <table>
            <SimpleTBody employees={employees} removeEmployee = {removeEmployee}></SimpleTBody>
        </table>
    </div>
}

export default SimpleTable;
