import React from 'react';
//Simple Component type
const TableBody = (data) => {
    //console.log('TableBody: ready to render...');
    //console.table(data.employeeData);
    const rows = data.employeeData.map(
        (item, index) => <tr key={index}><td>{item.name}</td><td>{item.job}</td></tr>
    );
    return rows;
}

//Class Component type
class Table extends React.Component{

   /* constructor() {
        super();
        //console.log('building Table Component');
    }*/
    render(){
        //console.log(`Table: ready to render...`);
        //console.table(this.props.employeesData);
        return(
            <table className="good">
                <thead>
                <tr>

                    <td>Name</td>
                    <td>Job</td>

                </tr>
                </thead>
                <tbody>
                    <TableBody employeeData={this.props.employeesData}></TableBody>
                </tbody>
            </table>
        )

    }

}

export default Table;
