import React,{Component} from "react";
import ApiForm from './ApiForm';
import {ApiOutput} from './ApiOutput';
class Api extends Component{
    getWPResult = async (value) => {
        const url = `https://en.wikipedia.org/w/api.php?action=opensearch&search=${value}&format=json&origin=*`;
        return fetch(
            url
        ).then(
            response => response.json()
        ).catch(
            err => console.warn(err)
        )
    }
    searchString = async (value) =>{
        //console.log('searching>>>>',value);
        const result = await this.getWPResult(value);
        this.setState(
            {
                searchResult: result,
            }
        )
    }
    constructor() {
        super();
        //console.log("Hi, API");
        this.state = {
            searchResult: null,
        }
    }
    render() {
        return (
            <div>
                <h3 className="good">
                    API
                </h3>
                <ApiForm search={this.searchString}></ApiForm>
                <ApiOutput data={this.state.searchResult}></ApiOutput>
            </div>
        )
    }
}
export default Api;
