import React,{Component} from "react";

class Form extends Component{
    initState = {
        name:'',
        job:''
    };
    constructor() {
        super();
        this.state = this.initState;
    }
    //changeHandler(event){....} //Non come metodo ma come arrow function
    changeHandler = (event)=>{
        const {name, value} = event.target;
        console.log('changeHandler', name, value);
        this.setState(
            {
                [name]: value
            }
        )
    }
    submitHandler = (event) => {
        event.preventDefault();
        this.props.addEmployee(this.state)
        this.setState(
            this.initState
        );
    }

    render(){
        const {name,job} = this.state;
        return (
        <form className="form" onSubmit={this.submitHandler}>

                    <label htmlFor="name">Name</label>
                    <input id="name" name="name" onChange={this.changeHandler} value={name}></input>

                    <label htmlFor="job" name="job">job</label>
                    <input id="job" name="job" value={job} onChange={this.changeHandler}></input>
                    <button type="submit">
                        Submit
                    </button>
        </form>
    )};
}

export default Form;
